export default function (Component) {
Component.options.__tip = "<p>If you customize weekday names by using the <code>dayNames</code>-prop you do that independently from <code>firstDayOfWeek</code>. This means that even though you may have set a different <code>firstDayOfWeek</code>, the first name specified in <code>dayNames</code> is always and will always be the name for <strong>Sunday</strong>.</p>\n"
}