export default "<tip>Use `v-is-inside`, in order to mark additional elements/components as being part of the inside. Anything marked with `v-is-inside` won't cause the outside-hanlder to be called.</tip>\n\n<template>\n  <div>\n    <FdAlert :dismissible=\"false\">\n      {{ message }}\n    </FdAlert>\n    <br /><br />\n    <div class=\"outside\">\n      <p>this is where the <strong>outside</strong> starts</p>\n      <div\n        class=\"inside\"\n        v-on-click-outside=\"clickOutside\"\n        @click=\"message = 'Click inside detected.'\"\n      >\n        <p>\n          <strong>inside:</strong> Clicks will not trigger a\n          <em>clickOutside</em>-event.\n        </p>\n      </div>\n      <div\n        class=\"inside\"\n        v-is-inside\n        @click=\"message = 'Click inside detected.'\"\n      >\n        <p>\n          <strong>inside:</strong> even though it is not inside a\n          <code>v-on-click-outside</code>-container.\n        </p>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data: () => ({ clickOutsideContext: \"context\", message: \"Click somewhere.\" }),\n  methods: {\n    clickOutside() {\n      this.message = \"Click outside detected.\";\n    }\n  }\n};\n</script>\n\n<style scoped>\n.outside > * {\n  margin: 30px 0;\n}\n\n.inside {\n  border: 1px solid #aaaaaa;\n  background-color: #fefefe;\n  color: black;\n  text-align: center;\n  vertical-align: middle;\n  padding: 20px;\n}\n.outside {\n  border: 1px solid #aaaaaa;\n  background-color: #fefefe;\n  color: black;\n  text-align: center;\n  vertical-align: middle;\n  padding: 20px;\n}\n</style>"