export default function (Component) {
Component.options.__tip = "<p>You can provide the visible title of the link by using a default slot or by using the <code>title</code>-prop.\nIf both are provided the <code>title</code>-prop is only used for the <code>title</code> attribute of the anchor. This means that</p>\n<pre><code class=\"language-html\">&lt;FdLink <span class=\"hljs-attribute\">href</span>=<span class=\"hljs-string\">\"#\"</span> <span class=\"hljs-attribute\">title</span>=<span class=\"hljs-string\">\"foo bar\"</span> /&gt;</code></pre>\n<p>is <strong>equivialent</strong> to</p>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">FdLink</span> <span class=\"hljs-attr\">href</span>=<span class=\"hljs-string\">\"#\"</span> <span class=\"hljs-attr\">title</span>=<span class=\"hljs-string\">\"foo bar\"</span>&gt;</span>foo bar<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">FdLink</span>&gt;</span></code></pre>\n"
}