export default function (Component) {
Component.options.__docs = "<p>Use an <code>FdButtonGroup</code> in order to group buttons. <code>FdButtonGroupButton</code> supports everything (ex. <code>icon</code>, <code>@click(…), …</code>) a regular <code>FdButton</code> does. You can use <code>v-model</code> on <code>FdButtonGroupButton</code>. The <code>value</code> of a button group is always an array which contains the <code>value</code> of each pressed/selected <code>FdButtonGroupButton</code>. You associate a value with a button group button by using it&#39;s <code>value</code>-prop. There are also three selection modes (specified by using the <code>selectionMode</code>-prop on the group):</p>\n<ol>\n<li><code>single</code>: A single button can be pressed/selected at a time.</li>\n<li><code>multiple</code>: Multiple buttons can be pressed/selected at a time.</li>\n<li><strong>A custom selection mode function</strong></li>\n</ol>\n<p><strong>Custom Selection Mode</strong></p>\n<p>If you have special needs you can specific a custom selection mode function. Simply pass a function to the <code>selectionMode</code>-prop. The function will be called with the current selection and the value of button that has just been clicked/tapped. Your custom selection mode function then has to return an array which represents the new selection.</p>\n<pre><code class=\"language-javascript\">const mySelectionMode = (selection, <span class=\"hljs-keyword\">value</span>) =&gt; {\n  <span class=\"hljs-keyword\">if</span>(<span class=\"hljs-keyword\">value</span> === <span class=\"hljs-string\">\"foo\"</span> || <span class=\"hljs-keyword\">value</span> === <span class=\"hljs-string\">\"bar\"</span>) {\n    <span class=\"hljs-keyword\">return</span> selection;\n  }\n  const index = selection.indexOf(<span class=\"hljs-keyword\">value</span>);\n  const newSelection = [...selection];\n  <span class=\"hljs-keyword\">if</span>(index &gt;= <span class=\"hljs-number\">0</span>) {\n    // <span class=\"hljs-keyword\">value</span> <span class=\"hljs-literal\">is</span> already selected,<span class=\"hljs-built_in\"> remove</span> it\n    newSelection.splice(index);\n    <span class=\"hljs-keyword\">return</span> newSelection;\n  }\n  // <span class=\"hljs-keyword\">value</span> <span class=\"hljs-literal\">is</span><span class=\"hljs-built_in\"> not</span> selected, add it\n  <span class=\"hljs-keyword\">return</span> [<span class=\"hljs-keyword\">value</span>, ...newSelection];\n};</code></pre>\n<p>The custom selection mode above ignores any interactions with buttons that have the value <code>foo</code> or <code>bar</code>. Otherwise it acts just like the multiple selection mode.</p>\n"
}