export default "<template>\n  <div class=\"grid\">\n    <div v-bg:action-1 />\n    <div v-bg:action-2 />\n    <div v-bg:text-1 />\n    <div v-bg:text-2 />\n    <div v-bg:text-3 />\n    <div v-bg:text-4 />\n    <div v-bg:text-5 />\n    <div v-bg:background-1 />\n    <div v-bg:background-2 />\n    <div v-bg:neutral-1 />\n    <div v-bg:neutral-2 />\n    <div v-bg:neutral-3 />\n    <div v-bg:neutral-4 />\n    <div v-bg:status-1 />\n    <div v-bg:status-2 />\n    <div v-bg:status-3 />\n    <div v-bg:status-4 />\n    <div v-bg:accent-1 />\n    <div v-bg:accent-2 />\n    <div v-bg:accent-3 />\n    <div v-bg:accent-4 />\n    <div v-bg:accent-5 />\n    <div v-bg:accent-6 />\n    <div v-bg:accent-7 />\n    <div v-bg:accent-8 />\n    <div v-bg:accent-9 />\n    <div v-bg:accent-10 />\n    <div v-bg:accent-11 />\n    <div v-bg:accent-12 />\n    <div v-bg:accent-13 />\n    <div v-bg:accent-14 />\n    <div v-bg:accent-15 />\n  </div>\n</template>\n\n<style scoped>\n.grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(12rem, 1fr));\n  grid-auto-rows: 1fr;\n}\n\n.grid::before {\n  content: \"\";\n  width: 0;\n  padding-bottom: 100%;\n  grid-row: 1 / 1;\n  grid-column: 1 / 1;\n}\n\n.grid > *:first-child {\n  grid-row: 1 / 1;\n  grid-column: 1 / 1;\n}\n\n.grid > * {\n  background: rgba(0, 0, 0, 0.1);\n  border: 1px lightgray solid;\n  margin: 5px;\n  border-radius: 10px;\n}\n</style>"