export default function (Component) {
Component.options.__docs = "<p><code>FdTable</code> supports three different selection modes:</p>\n<ol>\n<li>Selection mode <code>none</code> (default): Rows are not selectable in any way.</li>\n<li>Selection mode <code>single</code>: There can only ever be a single selected row.</li>\n<li>Selection mode <code>multiple</code>: Multiple rows can be selected.</li>\n</ol>\n<p>Use the <code>selectionMode</code>-prop to set the selection mode of your choice.</p>\n"
}