export default function (Component) {
Component.options.__docs = "<p>The example below is a little bit more complex. It highlights three aspects:</p>\n<h3 id=\"sortable-columns\">Sortable Columns</h3>\n<p>A header can be made sortable by simply flagging it as such using <code>sortable</code> and setting <code>sortBy</code> to any sortable property of the objects in your <code>items</code>-array. By doing so <code>FdTable</code> displays sort indicators in the individual column headers. The currently applied sorting can be adjusted by simply clicking on the column headers.</p>\n<h3 id=\"custom-cell-content\">Custom Cell Content</h3>\n<p>Up until now <code>FdTableCell</code> was only used to display plain text. However, <code>FdTableCell</code> is simply rendering whatever you put in it. This can be anything from emojis (we all like emojis, right? 😀 🥰 ) to images and even custom components.</p>\n<pre><code class=\"language-html\"><span class=\"xml\">// …\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">FdTableCell</span>&gt;</span></span><span class=\"hljs-template-variable\">{{item.rating}}</span><span class=\"xml\"><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">FdTableCell</span>&gt;</span>\n// …\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">FdTableCell</span>&gt;</span></span><span class=\"hljs-template-variable\">{{item.firstName}}</span><span class=\"xml\"> </span><span class=\"hljs-template-variable\">{{item.lastName}}</span><span class=\"xml\"><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">FdTableCell</span>&gt;</span>\n// …</span></code></pre>\n<p>The two lines above show that in action. The first cell is simply rendering the rating which is just a string with emojis. The second line is combining the first name with the last name in order to render the full name. But as already said: You can put in whatever you want.</p>\n<h3 id=\"modifying-the-displayed-items\">Modifying the displayed Items</h3>\n<p><code>FdTable</code> watches for any changes to it&#39;s items array. So you can simply add/remove and modify anything you want.</p>\n"
}