export default function (Component) {
Component.options.__docs = "<p>Use <code>FdTable</code> in order to display data best visualized by rows and columns.</p>\n<div class=\"tip\" style=\"border-left: 0.5rem solid #ffa94a;\">\n  <div class=\"tip-title\">IMPORTANT</div>\n  <div class=\"tip-body\">\n    <p>In a previous release you didn't have to put <code>FdTableRow</code> in a <code>template</code>. Going forward you have to do so.</p>\n  </div>\n</div>\n\n<p>The most important prop exposed by <code>FdTable</code> is called <code>items</code>. The items of a table must all be some kind of <code>Object</code>. If <code>FdTable</code> encounters an item without an <code>id</code>-property, an <code>id</code> will be created. The <code>id</code> of an item is used to uniquely identify the corresponding item. Each item may contain any additional custom properties.</p>\n<h3 id=\"fdtable-atonomy\">FdTable Atonomy</h3>\n<h4 id=\"the-table-header-fdtableheader\">The Table Header: <code>FdTableHeader</code></h4>\n<p>It is the responsibility of <code>FdTable</code> to render it&#39;s header. The header renders column names, sort indicators and custom content. You configure the header of an <code>FdTable</code> by using it&#39;s <code>headers</code>-prop. The <code>headers</code>-prop accepts an array which may contain strings or header configuration objects. A plain string inside the <code>headers</code>-array will be used as a label for the column header. The complete definition of the <code>headers</code>-prop looks like this:</p>\n<pre><code class=\"language-typescript\"><span class=\"hljs-keyword\">type</span> <span class=\"hljs-keyword\">Header</span> = {\n  label: <span class=\"hljs-built_in\">string</span>;\n  sortable?: <span class=\"hljs-built_in\">boolean</span>;\n  sortBy?: <span class=\"hljs-built_in\">string</span>;\n  alignment?: TextAlignment; <span class=\"hljs-comment\">// 'center' or 'default'</span>\n  renderHeader?: HeaderCellRenderFunction; <span class=\"hljs-comment\">// not yet implemented</span>\n} | <span class=\"hljs-built_in\">string</span>;</code></pre>\n<h4 id=\"the-table-row-template-fdtablerow\">The Table Row Template: <code>FdTableRow</code></h4>\n<p>Rows are rendered by <code>FdTable</code> on your behalf. This is why you don&#39;t specify each row individually. Instead you describe how to render a row (<code>FdTableRow</code>) given some context (using Vue&#39;s <strong>slot-scope</strong>-mechanism). The object passed to the template has the following shape:</p>\n<pre><code class=\"language-typescript\"><span class=\"hljs-comment\">// Object passed to the row template.</span>\n<span class=\"hljs-keyword\">interface</span> <span class=\"hljs-symbol\">RowSlotProps</span>&lt;<span class=\"hljs-symbol\">T</span>=<span class=\"hljs-symbol\">object</span>&gt; {\n  <span class=\"hljs-comment\">// Your custom item</span>\n  item: T &amp; { id: <span class=\"hljs-built_in\">string</span>; };\n\n  <span class=\"hljs-comment\">// True if the corresonding row is currently selected</span>\n  selected: <span class=\"hljs-built_in\">bool</span>ean;\n\n  <span class=\"hljs-comment\">// Call this if you want to change the selection</span>\n  <span class=\"hljs-comment\">// Of the current row. Typically bound to an</span>\n  <span class=\"hljs-comment\">// `@change`-event from `FdCheckbox`-instances.</span>\n  changeSelection(selected: <span class=\"hljs-built_in\">bool</span>ean, event: Event): <span class=\"hljs-built_in\">void</span>;\n}</code></pre>\n"
}