export default "<template>\n  <FdTable :headers=\"headers\" selectionMode=\"multiple\" :items=\"items\">\n    <template slot=\"row\" slot-scope=\"{ item }\">\n      <FdTableRow @click=\"onRowClick(item)\">\n        <FdTableCell>\n          <FdButton @click.stop=\"onButtonClick\">{{ item.firstName }}</FdButton>\n        </FdTableCell>\n        <FdTableCell>{{ item.lastName }}</FdTableCell>\n        <FdTableCell>{{ item.building }}</FdTableCell>\n      </FdTableRow>\n    </template>\n  </FdTable>\n</template>\n\n<script>\nexport default {\n  methods: {\n    onButtonClick() {\n      console.log(\"click on button\");\n    },\n    onRowClick(item) {\n      console.log(\"click on row representing item: %o\", item);\n    }\n  },\n  data: () => ({\n    headers: [\"First Name\", \"Last Name\", \"Building\"],\n    items: [\n      {\n        id: \"1\",\n        rating: 1,\n        firstName: \"Chris\",\n        lastName: \"Kienle\",\n        building: \"WFD02\"\n      },\n      {\n        id: \"2\",\n        rating: 2,\n        firstName: \"Andi\",\n        lastName: \"Kienle\",\n        building: \"WFD03\"\n      },\n      {\n        id: \"3\",\n        rating: 3,\n        firstName: \"Sven\",\n        lastName: \"Bacia\",\n        building: \"WFD02\"\n      },\n      {\n        id: \"4\",\n        rating: 4,\n        firstName: \"Artur\",\n        lastName: \"Raess\",\n        building: \"WFD02\"\n      }\n    ]\n  })\n};\n</script>"