export default "<template>\n  <div>\n    <FdTree :treeData=\"treeData\" :headers=\"headers\"> </FdTree>\n  </div>\n</template>\n\n<script>\nexport default {\n  data() {\n    return {\n      headers: [\n        \"Column Header\",\n        \"Column Header 1 \",\n        \"Column Header 2\",\n        \"Status\"\n      ],\n      treeData: [\n        {\n          columns: [\"First Level\", \"Data Col 2\", \"Data Col 3\", \"INACTIVE\"],\n          children: [\n            {\n              columns: [\"Child 1\", \"Data Col 2\", \"Data Col 3\", \"INACTIVE\"],\n              children: [\n                {\n                  columns: [\n                    \"Grandchild 1\",\n                    \"Data Col 2\",\n                    \"Data Col 3\",\n                    \"INACTIVE\"\n                  ],\n                  children: [\n                    {\n                      columns: [\n                        \"GreatGrandchild 1\",\n                        \"Data Col 2\",\n                        \"Data Col 3\",\n                        \"INACTIVE\"\n                      ]\n                    }\n                  ]\n                }\n              ]\n            },\n            {\n              columns: [\"Child 2\", \"Data Col 2\", \"Data Col 3\", \"INACTIVE\"]\n            }\n          ]\n        },\n        {\n          columns: [\"Row 2\", \"Data Col 2\", \"Data Col 3\", \"DEFAULT\"],\n          children: [\n            {\n              columns: [\"Child 1\", \"Data Col 2\", \"Data Col 3\", \"INACTIVE\"]\n            },\n            {\n              columns: [\"Child 2\", \"Data Col 2\", \"Data Col 3\", \"INACTIVE\"]\n            }\n          ]\n        },\n        {\n          columns: [\"Row 3\", \"Data Col 2\", \"Data Col 3\", \"INACTIVE\"]\n        },\n        {\n          columns: [\"Row 4\", \"Data Col 2\", \"Data Col 3\", \"INACTIVE\"],\n          children: [\n            {\n              columns: [\"Child 1\", \"Data Col 2\", \"Data Col 3\", \"INACTIVE\"]\n            }\n          ]\n        }\n      ]\n    };\n  }\n};\n</script>"