export default function (Component) {
Component.options.__tip = "<p><code>FdTable</code> is pretty fast. However it currently renders everything no matter whether an item is currently displayed or not. The <code>items</code>-array should be somewhere around <code>0</code> and <code>5.000</code>. Otherwise <code>FdTable</code> will crash your browser. We are working on removing that limitation. In the meantime you should work around that by using some kind of paging mechanism.</p>\n"
}