export default function (Component) {
Component.options.__docs = "<p>The <code>hasNext</code> and <code>hasPrevious</code> props can be used to control the next and previous buttons in the calendar header. Those props are functions that return either <code>true</code> or <code>false</code>. Returning <code>false</code> disables the corresponding button. The calendar passes an object containing the <code>min</code> and <code>max</code> date of the currently displayed month.</p>\n"
}