export default "<template>\n  <div>\n    <FdTable :headers=\"headers\" :items=\"items\">\n      <template slot=\"row\" slot-scope=\"{ item }\">\n        <FdTableRow>\n          <FdTableCell>{{ item.rating }}</FdTableCell>\n          <FdTableCell>{{ item.firstName }}</FdTableCell>\n          <FdTableCell>{{ item.lastName }}</FdTableCell>\n          <FdTableCell>{{ item.firstName }} {{ item.lastName }}</FdTableCell>\n        </FdTableRow>\n      </template>\n    </FdTable>\n\n    <h2>Creating an Entry</h2>\n\n    <FdFormSet>\n      <FdFormItem label=\"First Name\">\n        <FdInput\n          v-model=\"newEntry.firstName\"\n          placeholder=\"Enter something nice\"\n        />\n      </FdFormItem>\n      <FdFormItem label=\"Last Name\">\n        <FdInput\n          v-model=\"newEntry.lastName\"\n          placeholder=\"Enter something nice\"\n        />\n      </FdFormItem>\n      <FdFormItem label=\"Rating\">\n        <FdCombobox v-model=\"newEntry.rating\">\n          <FdMenuItem value=\"️️️⭐️\">⭐️</FdMenuItem>\n          <FdMenuItem value=\"⭐️⭐️\">⭐️⭐️</FdMenuItem>\n          <FdMenuItem value=\"⭐️⭐️⭐️\">⭐️⭐️⭐️</FdMenuItem>\n          <FdMenuItem value=\"⭐️⭐️⭐️⭐️\">⭐️⭐️⭐️⭐️</FdMenuItem>\n          <FdMenuItem value=\"⭐️⭐️⭐️⭐️⭐️\">⭐️⭐️⭐️⭐️⭐️</FdMenuItem>\n        </FdCombobox>\n      </FdFormItem>\n    </FdFormSet>\n    <FdButton @click=\"addCurrentEntry\">Add Entry</FdButton>\n  </div>\n</template>\n\n<script>\nexport default {\n  methods: {\n    addCurrentEntry() {\n      const entry = {\n        ...this.newEntry\n      };\n      this.items = [...this.items, entry];\n      this.newEntry = {\n        firstName: null,\n        lastName: null,\n        rating: \"⭐️⭐️⭐️\",\n        id: String(this.items.length + 1)\n      };\n    }\n  },\n  data() {\n    return {\n      headers: [\n        { label: \"Rating\", sortable: true, sortBy: \"rating\" },\n        { label: \"First Name\", sortable: true, sortBy: \"firstName\" },\n        { label: \"Last Name\", sortable: true, sortBy: \"lastName\" },\n        { label: \"Full Name\", sortable: true, sortBy: \"firstName\" }\n      ],\n      newEntry: {\n        firstName: null,\n        lastName: null,\n        rating: \"⭐️⭐️⭐️\",\n        id: \"5\"\n      },\n      items: [\n        {\n          id: \"1\",\n          rating: \"⭐️⭐️⭐️\",\n          firstName: \"Chris\",\n          lastName: \"Kienle\"\n        },\n        {\n          id: \"2\",\n          rating: \"⭐️⭐️⭐️⭐️\",\n          firstName: \"Andi\",\n          lastName: \"Kienle\"\n        },\n        {\n          id: \"3\",\n          rating: \"⭐️⭐️⭐️⭐️⭐️\",\n          firstName: \"Sven\",\n          lastName: \"Bacia\"\n        },\n        {\n          id: \"4\",\n          rating: \"⭐️⭐️⭐️⭐️⭐️\",\n          firstName: \"Artur\",\n          lastName: \"Raess\"\n        }\n      ]\n    };\n  }\n};\n</script>"