export default function (Component) {
Component.options.__docs = "<p>In some situations you may want a checkbox in addition to the regular row selection indicating the currently selected rows. As it turns out you could do that completely on your own by simply using <code>FdCheckbox</code> and configuring it correctly. However <code>FdTable</code> comes with a special component called <code>FdRowSelectionIndicator</code> that should be preferred over plain <code>FdCheckbox</code>es whenever possible. The reason is that we want to hide the fact that the selection is indicated by using a checkbox in order to change that in future releases.</p>\n"
}