export default function (Component) {
Component.options.__docs = "<p>By default the first day of the week is <strong>Sunday</strong>. You can customize that by using the <code>firstDayOfWeek</code>-prop. <code>firstDayOfWeek</code> accepts a number between <code>0</code> (default) and <code>6</code>. By setting <code>firstDayOfWeek</code> to <code>1</code> you basically set the first day of the week to <strong>Monday</strong>.</p>\n"
}