export default "<template>\n  <div>\n    <FdButton @click=\"visible = !visible\">Toggle Visibile</FdButton>\n\n    <br /><br />\n\n    <FdAlert type=\"success\" v-model=\"visible\" dismissible>\n      Success alert. Click on the close button.\n    </FdAlert>\n  </div>\n</template>\n\n<script>\nexport default {\n  data() {\n    return {\n      visible: true\n    };\n  }\n};\n</script>"