export default function (Component) {
Component.options.__docs = "<p><strong>Implementation Details</strong></p>\n<p>There are three different ways a trigger control can be rendered.</p>\n<ol>\n<li><code>$slots.control</code>: If there is a slot called <code>control</code> we simply render that. This control should emit <code>click</code>-events.</li>\n<li><code>$scopedSlots.control</code>: If there is a scoped slot <code>control</code> we assume that the popover consumer (<strong>you</strong>) has some kind of special needs. Thus, when rendering the scoped slot, we pass a little bit of context to the consumer.</li>\n</ol>\n<p><strong>The context looks like this:</strong>:</p>\n<pre><code class=\"language-javascript\">{\n   <span class=\"hljs-comment\">// Calling this will toggle the popover.</span>\n   toggle: <span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> (<span class=\"hljs-built_in\">void</span>);\n   <span class=\"hljs-comment\">// whether the popover is currently visible.</span>\n   visible: <span class=\"hljs-built_in\">boolean</span>;\n}</code></pre>\n<p>Because of the fact that we assume that the consumer has special needs, we do not show the popover automatically. The consumer has to call <code>toggle</code> for example by binding it to <code>@click</code>/<code>@click.native</code> of some kind of control or by using <code>v-model</code>.</p>\n<ol start=\"3\">\n<li>If there is no (scoped) control-slot we simply render a standard button on behalf of the consumer.</li>\n</ol>\n"
}